import React, {useState , useEffect, useRef} from 'react';
import Popup from 'reactjs-popup';
import { useSpring, animated } from 'react-spring'
import classes from './popUp.module.css'
import { request, dateFormat } from '../helper';
import Button from "@material-ui/core/Button";
import 'reactjs-popup/dist/index.css';


const options = [
  { value: 'Appointment', label: 'Appointment' },
  { value: 'Callback', label: 'Callback' },
  { value: 'Closer', label: 'Closer' },
  { value: 'Reject', label: 'Reject' },
];

const PopUp = (props) => {
    const [selectedStatus, setSelectedStatus] = useState('');
    const [mobileNumber, setMobileNumber] = useState('')


    const status  = useRef('');
    const reason = useRef('');
    const remark = useRef('');

    const changeStatus = (e) =>{
      setSelectedStatus(e.target.value)
    }

    
    const submitHandler = async () => {
      const update = {
        mobile : props.customerData.mobile,
        status : status.current ? status.current.value : null,
        reason : reason.current ? reason.current.value : null,
        remark : remark.current ? remark.current.value : null
      }

      let res = await request("customer/update").post(update);
      if(res.status){
      }
      console.log(update)
      props.handler();
    }  

  
    const styleProps = useSpring({
      opacity: 1,
      from: { opacity: 0 }
    }) 


    const ReasonAndRemarkFunction = ()=>{
      switch (selectedStatus) {
        case 'Appointment':
        case 'Callback':
          return (
                <input type="text" ref={remark} placeholder="Remark"></input>
              )  
        case 'Closer':
        case 'Reject' :
          return (

                <input type="text" ref={reason}  placeholder="Reason"></input>
          ) 
        default:
          break;
      }
    }


    const buttonStyle ={
      backgroundColor:'#42184e', 
      cursor: 'pointer', 
      color:'#fff' 
    }

      
    return(    
        <Popup  trigger={<button className="button" style={buttonStyle}> {props.customerData.status} </button>} modal nested> 
           {close => (
            <div className={classes.Popup_form}>
            {/* <form className={classes.Popup_form}>  */}

            <table className="table table-bordered">
                <thead>
                  <tr>
                    <th scope="col">Customer Name</th>
                    <th scope="col">Mobile Number</th>
                    <th scope="col">Email ID</th>
                    <th scope="col">Location</th>
                    <th scope="col">TC ID</th>
                    <th scope="col">TC Name</th>
                    <th scope="col">Branch</th>
                  </tr>
                </thead>
                <tbody>
                  {
                        <tr>
                          <td><small>{props.customerData.first_name} {props.customerData.last_name}</small></td>
                          <td><small >{props.customerData.mobile}</small></td>
                          <td ><small>{props.customerData.email}</small></td>
                          <td><small>{props.customerData.location}</small></td>
                          <td><small>{props.customerData.employee_id}</small></td>
                          <td><small>{props.customerData.firstname}</small></td>
                          <td><small>{props.customerData.branch}</small></td>
                        </tr>
                    }
                </tbody>
              </table>
              <div className={classes.Popup_status}>
                    <select className="form-control" ref={status} defaultValue={props.customerData.status} onChange={e => {changeStatus(e)}}> 
                        <option value='Appointment'>Appointment</option>
                        <option value='Callback'>Callback</option>
                        <option value='Closer'>Closer</option>
                        <option value='Reject'>Reject</option>
                    </select>
              </div>
              <div className={classes.Popup_feedback}>
              {ReasonAndRemarkFunction()}
              </div>
              <div className={classes.Popup_button}>
              <Button className="mx-auto"  variant="contained" color="primary" onClick={() => {close(); submitHandler();}}>Submit</Button>
              </div>
            {/* </form> */}
            </div>
              )}  
        </Popup>
  )};
export default PopUp;