import React, { Fragment } from "react";
import "react-datepicker/dist/react-datepicker.css";
import DatePicker from "react-datepicker";
import {request, dateFormat} from "../helper";
import Button from "@material-ui/core/Button";
import { withRouter } from "react-router-dom";

class AddEmployee extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      enteredEmployeeId : {},
      employeeDetails : '',
      employeeDesignation : '',
      employeeRole : '',
      locations : [],
      changedLastUpdateDate: new Date(),
      changedUserUpdateDate: new Date(),
    }

    this.title = React.createRef('');
    this.firstname = React.createRef('');
    this.lastname = React.createRef('');
    this.employeeId = React.createRef('');
    this.email = React.createRef('');
    this.password = React.createRef('');
    this.mobile = React.createRef('');
    this.gender = React.createRef('');
    this.designation = React.createRef('');
    this.location = React.createRef('');
    this.branchName = React.createRef('');
    this.employeeState = React.createRef('');
    this.region = React.createRef('');
    this.role = React.createRef('');
    this.higherPosition = React.createRef('');

    this.titles = ["Mr", 'Mrs', 'Miss'];
    this.gender = ["Male", 'Female'];
    this.states = ['Andhra Pradesh', 'Kerala', 'Karnataka', 'Telangana', 'Tamilnadu', 'New Delhi', 'Haryana', 'Jammu and Kashmir', 'Uttar Pradesh', 'Punjab', 'Uttarakhand', 'Punjab', 'Uttarakhand', 'Goa', 'Maharashtra', 'Rajasthan'
    ,'Gujarat', 'Bihar', 'Orissa', 'Jharkhand', 'West Bengal', 'Chattisgarh', 'Madhya Pradesh', 'Sikkim', 'Tripura', 'Himachal Pradesh', 'Puducherry', 'Assam', 'Arunachal Pradesh', 'Mizoram', 'Meghalaya', 'Manipur', 'Nagaland'];
    this.regions = ['South', 'East', 'North', 'West', 'Central'];
    this.roles=[ "Admin", "NSM", "RSM", 'Zonal Manager', 'Branch Manager',"Telesales Manager", "Senior Relationship Manager", "Relationship Manager", 'Senior Team Lead', 'Team Lead', 'Telecaller', 'PTL', 'HCL', 'ITL', 'Telecaller']
  }


  componentDidMount() {
    (async () => {
      let desg = await request("designation/all").get();
      let jsonn = await desg.json();

      let bran = await request("branch/all").get();
      let resp = await bran.json();

      this.setState({
        designations: jsonn.message,
        branches: resp.message,
      })

      for(const location of resp.message) {
            if(!this.state.locations.includes(location.location)) {
                this.state.locations.push(location.location);
            }
      }

    const res = await request(`employee/select/${this.props.location.state.id}`).get()
    const json = await res.json();

    this.setState(
        {
          employeeDetails : json.message[0],
          changedLastUpdateDate : json.message[0]?.last_update_date,
          changedUserUpdateDate : json.message[0]?.user_update_date,
          employeeDesignation : json.message[0].designation,
          employeeRole : json.message[0].role
        }
        )
    })();
  }

  renderHigherPositionInput = (SelectedRole) =>{
    return(
              <Fragment>
                <div className="d-flex justify-content-between">
                  <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0 inputField">{SelectedRole} ID</label>
                    <input type="text" ref={this.higherPosition} defaultValue={this.state.employeeDetails?.higher_position} name="higher_position" className="form-control" placeholder={`${SelectedRole} ID`} autoComplete="off" />
                  </div>
                  <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                  <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                </div>
              </Fragment>
    )
}

higherPositionFunction = ()=>{
  switch (this.state.employeeRole) {
    case "Telecaller":
      return(
             this.renderHigherPositionInput("Team Lead")
            )
    case "Zonal Manager":
      return(
        this.renderHigherPositionInput("RSM")
            )
    case "Branch Manager":
    case "Telesales Manager":
      return(
        this.renderHigherPositionInput("Zonal Manager")
            ) 
    case "Senior Relationship Manager":
      return(
        this.renderHigherPositionInput("Branch Manager")
            )
    case "Relationship Manager":
      return(
        this.renderHigherPositionInput("Senior Relationship Manager")
            )
    case "Senior Team Lead":
      return(
        this.renderHigherPositionInput("Telesales Manager")
            )
    case "Team Lead":
    case "PTL":
    case "ITL":
    case "HTL":
      return(
        this.renderHigherPositionInput("Senior Team Lead")
            )
    default:
      break;
  }
}


  handleChange = () => {
  //   // this.setState({
  //   //   [event.target.name]: event.target.value
  //   // })
  }

  check = () => {
    if (isNaN(Number(this.state.employee_id))) {
      alert("Please Enter valid Employee Id");
      return false;
    } else if (isNaN(Number(this.state.mobile))) {
      alert("Please Enter valid Mobile Number");
      return false;
    }

    return true;
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      employee_id: Number(this.employeeId.current.value),
      title: this.title.current.value,
      firstname: this.firstname.current.value,
      lastname: this.lastname.current.value,
      email: this.email.current.value,
      password: this.password.current.value,
      mobile: Number(this.mobile.current.value),
      gender: this.gender.current.value,
      designation: this.designation.current.value,
      location: this.location.current.value,
      branch: this.branchName.current.value,
      last_update_date : dateFormat(new Date(this.state.changedLastUpdateDate).toLocaleDateString()),
      user_update_date : dateFormat(new Date(this.state.changedUserUpdateDate).toLocaleDateString()),
      state: this.employeeState.current.value,
      region: this.region.current.value,
      role: this.role.current.value,
      higher_position : Number(this.higherPosition.current !== null ? this.higherPosition.current.value : 0)
    }


    let res = await request("employee/update").post(data);
    let text = await res.text();
    // let json = await res.json();
    if(res.status === 200){
      alert("Employee Details Updates Sucessfully")
    }
  };

  // teamLeadId(){
  //       if(this.state.designation === "Telecaller" || this.state.employeeRole === "Telecaller"){
  //         return (
  //           <div className="d-flex justify-content-between">  
  //               <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
  //               <label htmlFor="exampleInputPassword1" className="mb-0">Team Lead ID (Optional)</label>
  //                 <input type="text" ref={this.higherPosition}  name="team_lead_id" className="form-control" placeholder="Team Lead Id" autoComplete="off" />
  //               </div>
  //               <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
  //               <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
  //           </div>
  //         )
  //       } 
  // }

  onSubmitHandler = async (e) => {
    // e.preventDefault();
    // this.setState({enteredEmployeeId: e.target[0].value})

    // const res = await request(`employee/select/${e.target[0].value}`).get()
    // const json = await res.json();

    // this.setState(
    //     {
    //       employeeDetails : json.message[0],
    //       changedLastUpdateDate : json.message[0]?.last_update_date,
    //       changedUserUpdateDate : json.message[0]?.user_update_date,
    //       employeeDesignation : json.message[0].designation,
    //       employeeRole : json.message[0].role
    //     }
    //     )
  }


  render() {
    return (
      <Fragment>
        <section className="mx-auto col-sm-12 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Employee Details</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit}>
              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label className="mb-0">Employee ID</label>
                  <input type="text" placeholder="Employee ID" name="employee_id" autoComplete="off"
                     readOnly ref={this.employeeId}  defaultValue={this.state.employeeDetails?.employee_id} className="form-control inputField" required />
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 d-flex" style={{ display: "block" }}>
                  <div className="col-sm-4 pr-0 pl-0">
                    <label htmlFor="exampleInputPassword1" className="mb-0">Title</label>
                    <select required className="form-control inputField" ref={this.title} name="title">Title
                      <option value="">Select Title</option>
                      {
                         this.titles.map( title => {
                               if(title === this.state.employeeDetails?.title){
                                      return <option selected value={title}>{title}</option>
                                     }
                                 return <option value={title}>{title}</option>
                               })  
                       }
                    </select>
                  </div>

                  <div className="col-sm-9 pl-0">
                    <label htmlFor="exampleInputPassword1" className="mb-0">First Name</label>
                    <input type="text" onChange={this.handleChange} ref={this.firstname}  defaultValue={this.state.employeeDetails.firstname} name="firstname" required className="form-control inputField" placeholder="First Name" autoComplete="off" />
                  </div>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Last Name</label>
                  <input type="text" onChange={this.handleChange}  ref={this.lastname}  defaultValue={this.state.employeeDetails.lastname} name="lastname" required className="form-control inputField" placeholder="Last Name" autoComplete="off" />
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Mobile Number</label>
                  <input type="text" onChange={this.handleChange} ref={this.mobile}  defaultValue={this.state.employeeDetails?.mobile} name="mobile" required className="form-control inputFields" aria-describedby="emailHelp" placeholder="Mobile Number" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">-
                  <label htmlFor="exampleInputPassword1" className="mb-0">Email</label>
                  <input type="email" onChange={this.handleChange} ref={this.email}  defaultValue={this.state.employeeDetails?.email} name="email" required className="form-control inputField" placeholder="Email" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Password</label>
                  <input type="password" onChange={this.handleChange} ref={this.password}  defaultValue={this.state.employeeDetails?.password} name="password" required className="form-control inputField" placeholder="Password" autoComplete="off" />
                </ div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Gender</label>
                  <select required className="form-control inputField" ref={this.gender}  onChange={this.handleChange} name="gender">
                    <option value="">select Gender</option>
                    {
                         this.gender.map(value => {
                               if(value === this.state.employeeDetails?.gender){
                                      return <option selected value={value}>{value}</option>
                                     }
                                 return <option value={value}>{value}</option>
                               })  
                       }
                  </select>
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3" style={{marginTop:'28px'}} >
                  <label htmlFor="exampleInputPassword1" className="mb-0">Last Updated Date</label>
                  <DatePicker required className="form-control inputField"  placeholderText="Select Date" showPopperArrow={false}
                     selected={new Date(this.state.changedLastUpdateDate ? this.state.changedLastUpdateDate : new Date())} onChange={date => this.setState({changedLastUpdateDate : date})} onKeyDown={(e) => e.preventDefault()} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3" style={{marginTop:'28px'}}>
                  <label htmlFor="exampleInputPassword1" className="mb-0">User Updated Date</label>
                  <DatePicker required className="form-control inputField" selected={this.state.userdate} placeholderText="Select Date" showPopperArrow={false}
                  selected={new Date(this.state.changedLastUpdateDate ? this.state.changedUserUpdateDate : new Date())} onChange={date => this.setState({changedUserUpdateDate : date})} onKeyDown={(e) => e.preventDefault()} />
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Designation</label>
                  <select required className="form-control inputField" ref={this.designation} name="designation" >
                    <option value="">select Designation</option>
                    {
                         this.state.designations?.map((value) => {
                               if(value.designation === this.state.employeeDetails?.designation){
                                      return <option  selected value={value.designation}>{value.designation}</option>
                                     }
                                 return <option value={value.designation}>{value.designation}</option>
                               })  
                       }
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Location</label>
                  <select required className="form-control inputField" ref={this.location}  id="location" onChange={this.handleChange} name="location" >
                    <option value="">select Location</option>
                    {
                         this.state.locations.map(value => {
                               if(value === this.state.employeeDetails?.location){
                                      return <option selected value={value}>{value}</option>
                                     }
                                 return <option value={value}>{value}</option>
                               })  
                       }
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Branch Name</label>
                  <select required className="form-control inputField" ref={this.branchName}  onChange={this.handleChange} name="branch" >
                    <option value="">select Branch Name</option>
                    {/* {
                      this.state.branches.map((item, index) => {
                        return (
                          <option key={item.branch + index} value={item.branch}>
                            {item.branch}
                          </option>
                        )
                      })
                    } */}
                    {
                         this.state.branches?.map((value) => {
                               if(value.branch === this.state.employeeDetails?.branch){
                                      return <option  selected value={value.branch}>{value.branch}</option>
                                     }
                                 return <option value={value.branch}>{value.branch}</option>
                               })  
                       }
                  </select>
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">State</label>
                  <select required className="form-control inputField" ref={this.employeeState}  id="StateName" onChange={this.handleChange} name="state" >
                    <option value="">Select State</option>
                    {
                         this.states?.map(value => {
                               if(value === this.state.employeeDetails?.state){
                                      return <option selected value={value}>{value}</option>
                     
                                     }
                                 return <option value={value}>{value}</option>
                               })  
                       }
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Region</label>
                  <select required className="form-control inputField" id="roleName" ref={this.region}  onChange={this.handleChange} name="region">
                    <option value="">Select Region</option>
                    {
                         this.regions?.map(value => {
                               if(value === this.state.employeeDetails?.region){
                                      return <option selected value={value}>{value}</option>
                            
                                     }
                                 return <option value={value}>{value}</option>
                               })  
                       }
                  </select>
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Role</label>
                  <select required className="form-control inputField" ref={this.role}  id="roleName" onChange={this.handleChange} name="role" >
                    <option value="">Select Role</option>
                    {
                         this.roles?.map(value => {
                               if(value === this.state.employeeDetails?.role){
                                      return <option selected value={value}>{value}</option>
                                     }
                                 return <option value={value}>{value}</option>
                               })  
                       }
                  </select>
                </div>
              </div>
              {this.higherPositionFunction()}
              <div className="d-flex" style={{ width: "100%" }}>
                <button type="submit" className="btn btn-primary d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)",marginTop:"50px", marginLeft:"15px", color: "white", border: "none" }}>Submit</button>
              </div>
            </form>
          </div>
        </section>
      </Fragment>
    );
  }
}

export default withRouter(AddEmployee);