function renderSideBar(path, role) {
  switch (role) {
    case 'Zonal Manager':
    case 'Admin':
      return renderAll(path); 
    case 'Sr.Relationship Manager':
    case 'Regional Manager':
      return renderRelationalManager(path);
    case 'Branch Manager':
      return renderBranchManager(path);
    case "Telecaller":
    case "Senior Team Leader":
    case 'Relationship Manager':
      return renderTeleCaller(path);
    default:
      return renderAll(path);
  }
}


function returnId(data) {
  if (data === undefined || data.status === 404 || data === "" || ('employee_id' in data) === false) {
    return localStorage.getItem('_id');
  }
  return data.employee_id;
}

/////////////////////////////////////////////////////////////// MAIN COMPONENT

const MyComponent = (props) => {
  const [data, setData] = useState({});
  const [role, setRole] = useState('');
  const [graphs, setGraphs] = useState({});
  const [displayTable, setDisplayTable] = useState([]);
  const [sideBarToogle, setSideBarToogle] = useState(false);
  const [graphTable, setGraphTable] = useState(false);

  const reRenderFunction = async ()=>{
      const data = await request(`employee/select/${returnId(props.location.state)}`).get()
      const response = await data.json();
      setData(response.message[0]);
      setRole(response.message[0].role);

   const employeeId =  localStorage.getItem('_id');
    const graphs = await request(`reports/graphs/${employeeId}`).get();
    const json = await graphs.json();
    setGraphs(json);
  }

  useEffect( async () => {
    (localStorage.length === 0) && localStorage.setItem('_id', props.location.state.employee_id);
   reRenderFunction();
  }, []);

  


  const displayTableHandler = (e, text) => {
    switch(text){
      case "appointments" : 
          setDisplayTable(graphs.appointments)
      break;
      case "callbacks" : 
          setDisplayTable(graphs.callbacks)
      break;
      case "closers" : 
          setDisplayTable(graphs.closers)
      break;

      default:
        break;
    }

    setGraphTable(true)
  }

  const popUpHandler = () =>{
    reRenderFunction();
  }

  function logout() {
    if (window.confirm("Are you sure you want to Logout ?")) {
      localStorage.clear();
      props.history.push({
        pathname: `/`
      });
    }
  };

  let { path, url } = useRouteMatch();


  const styleProps = useSpring({
      opacity : 1,
      marginLeft : 0,
      from : {opacity : 0, marginLeft:-500}
  });

  var toggleSideBar = () => {
    setSideBarToogle(!sideBarToogle)
    // active
  }

  MyComponent.toggleSideBar = toggleSideBar;
  return (
    <Fragment>
    <animated.div style={styleProps}>
      {/* <div className="Home_header_logo">
      <Header/>
      </div> */}
    <div className="Home_main_div">
      
      <div className="Home_header_div">
        <div className="Home_sidebar_button" onClick={toggleSideBar}><i className="fas fa-bars"></i></div>
        <h4 className="p-2 align-self-center" style={{ color: "white", marginTop:"9px" }}>DAR for Investment Services</h4>
        <h6 className="m-0 p-0 align-self-center text-white"><strong>Role:</strong> {role}</h6>
        <button className="btn p-1 align-self-center" onClick={() => logout()}>Logout</button>
      </div>
        

      <div className="Home_content">
        
        <div className={["Home_sidebar", sideBarToogle ? "pro-sidebar-open" : "pro-sidebar-close" ].join(" ")}>
        {renderSideBar(url, role)}
        </div>
        
        <section className="Home_content_section">
          <Switch>
            <Route exact path={url}>
              <div className="Home_main_container"> 
                  <div className="Home_sub_container" onClick={(e) => displayTableHandler(e, "appointments")}>
                      <h4 className="Home_sub_text">Today's Appointment Reposts</h4>
                      <h3 className="icon"><i className="far fa-calendar-check "></i></h3>
                      <h1 className="Home_sub_text">{graphs.appointments?.length}</h1>
                  </div> 
                  <div className="Home_sub_container" onClick={(e) => displayTableHandler(e, "callbacks")}>
                      <h4 className="Home_sub_text">Today's Callback Reposts</h4>
                      <h3 className="icon"><i className="fas fa-headset"></i></h3>
                      <h1 className="Home_sub_text">{graphs.callbacks?.length}</h1>
                  </div> 
                  <div className="Home_sub_container" onClick={(e) => displayTableHandler(e, "closers")}>
                      <h4 className="Home_sub_text">Today's Closer Reposts</h4>
                      <h3 className="icon"><i className="far fa-times-circle"></i></h3>
                      <h1 className="Home_sub_text">{graphs.closers?.length}</h1>
                  </div> 
              </div>

              <div style={{display : graphTable ? "initial" : "none"}}>
              <div className="Home_table" style={{overflowX : "scroll"}}>
                  <table className="table table-bordered">
                    <thead>
                      <tr>
                        <th scope="col">S.No</th>
                        <th scope="col">Customer Name</th>
                        <th scope="col">Product Name</th>
                        <th scope="col">Mobile Number</th>
                        <th scope="col">Email ID</th>
                        <th scope="col">Location</th>
                        <th scope="col">State</th>
                        <th scope="col">TC ID</th>
                        <th scope="col">TC Name</th>
                        <th scope="col">Branch</th>
                        <th scope="col">Status</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        displayTable?.map((item, index) => {
                          return (
                            <tr  key={index}>
                              <th scope="row">{index + 1}</th>
                              <td><small>{item.first_name} {item.last_name}</small></td>
                              <td><small>{item.product}</small></td>
                              <td><small>{item.mobile}</small></td>
                              <td><small>{item.email}</small></td>
                              <td><small>{item.location}</small></td>
                              <td><small>{item.present_state}</small></td>
                              <td><small>{item.employee_id}</small></td>
                              <td><small>{item.firstname}</small></td>
                              <td><small>{item.branch}</small></td>
                              <td><Popup customerData = {item} handler={popUpHandler}/></td>
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </table>
              </div>
              </div>
            </Route>
            <Route path={`${url}/AddEmployee`}>
              <AddEmployee />
            </Route>
            <Route path={`${url}/EmployeeDetails`}>
              <EmployeeDetails />
            </Route>
            <Route path={`${url}/BranchDetails`}>
              <BranchDetails />
            </Route>
            <Route path={`${url}/AddDesignation`}>
              <AddDesignation />
            </Route>
            <Route path={`${url}/AddNewCustomer`}>
              <AddNewCustomer />
            </Route>
            <Route path={`${url}/Customers`}>
              <ShowCustomers />
            </Route>
            <Route path={`${url}/Appointments`}>
              <Appointments />
            </Route>
            <Route path={`${url}/Callbacks`}>
              <CallBacks />
            </Route>
            <Route path={`${url}/Closers`}>
              <Closers />
            </Route>
            <Route path={`${url}/Rejects`}>
              <Rejects />
            </Route>
            <Route path={`${url}/Existingcustomer`}>
              <ExistingCustomer  />
            </Route>
            <Route path={`${url}/Branch/Appointments`}>
              < BranchAppointments/>
            </Route>
            <Route path={`${url}/Branch/Callbacks`}>
              <BranchCallBacks />
            </Route>
            <Route path={`${url}/Branch/Closers`}>
              <BranchClosers  />
            </Route>
            <Route path={`${url}/Branch/Rejects`}>
              <BranchRejects  />
            </Route>
            <Route path={`${url}/EmployeeUpdation`}>
              <EmployeeUpdation />
            </Route>
            
          </Switch>
        </section>
      </div>
    </div>
          
    </animated.div>
    </Fragment>
  );
};

export default MyComponent;